body {
  margin: 0;
  padding: 0;
  background: linear-gradient(to right, rgb(44, 110, 176), rgb(23, 55, 37)) repeat scroll 0% 0%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
font-family: 'SEGA LOGO FONT';
font-style: normal;
font-weight: normal;
src: local('SEGA LOGO FONT'), url(/robots/static/media/SEGA.12a3db8b.woff) format('woff');
}

h1{
	font-family: 'SEGA LOGO FONT';
	font-weight: 200;
	color: #0ccaa4;
}
